.listItem {
  display: flex;
  margin: 16px 0;
  // background-color: var(--main-color);
  // margin-right: 5px;
  // overflow: hidden;
  // cursor: pointer;
  // color: white;
  // padding: 5px;

  .movieListMain{
    display: inline-table;
    flex: 1 0 auto;
    list-style-type: none;
    margin: 0 12px 0 0;
    position: relative;
  
  }

  .nm-collections-title-name{
    color: #fff;
    display: block;
    margin-top: 2px;
    text-align: center;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  video {
    width: 100%;
    height: 140px;
    object-fit: cover;
    top: 0;
    left: 0;
  }

  .movieDetail {
    width: 325px;
    height: 300px;
    position: absolute;
    top: -150px;
    -webkit-box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.07);
    box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.07);
    border-radius: 5px;
    background-color: #000;
    color: #fff;

    img {
      height: 140px;
    }

    .itemInfo {
      display: flex;
      flex-direction: column;
      padding: 5px;

      .icons {
        display: flex;
        margin-bottom: 10px;

        .icon {
          border: 2px solid white;
          padding: 5px;
          border-radius: 50%;
          margin-right: 10px;
          font-size: 16px;
        }
      }
      .itemInfoTop {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        font-size: 14px;
        font-weight: 600;
        color: gray;

        .limit {
          border: 1px solid gray;
          padding: 1px 3px;
          margin: 0 10px;
        }
      }

      .desc {
        font-size: 13px;
        margin-bottom: 10px;
      }

      .genre {
        font-size: 14px;
        color: lightgray;
      }
    }
  }
}
